/* Tailwind (if your project already builds Tailwind locally, you can remove this) */
@import url('https://cdn.jsdelivr.net/npm/tailwindcss@2.2.19/dist/tailwind.min.css');

/* ===== Containers / Layout helpers ===== */
.zoom-out-container {
  transform-origin: top left;
  transition: transform 0.3s ease;
}

.expanded {
  position: fixed;
  top: 5vh;
  left: 5vw;
  width: 90vw;
  height: 90vh;
  background: rgba(0, 0, 0, 0.95);
  border-radius: 10px;
  z-index: 1000;
  transform: scale(1);
  overflow: auto;
}

/* ===== Sidebar (desktop pinned, mobile slide-in) ===== */
.sidebar {
  position: fixed;
  top: 0;
  left: 0; /* Desktop visible */
  height: 100%;
  width: 250px; /* Match Admin main margin-left */
  background: rgba(26, 26, 46, 0.45);
  color: white;
  padding: 20px;
  z-index: 1001;
  transition: left 0.3s ease;
  border-radius: 0 0.75rem 0.75rem 0;
  border-right: 2px solid #00d4ff;
}

.sidebar h2 {
  margin-top: 0 !important;
  margin-bottom: 1.5rem !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  color: #00d4ff;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar ul li {
  padding: 5px 0;
  color: #00d4ff;
  cursor: pointer;
  border-radius: 0.5rem;
}

.sidebar ul li:hover {
  background: #16213e;
}

/* Sidebar button base */
.sidebar-button {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: white;
  text-align: center;
  border-radius: 8px;
  background: linear-gradient(145deg, #2b2e3c, #1c1f2a);
  box-shadow:
    inset 3px 3px 7px rgba(255, 255, 255, 0.1),
    inset -3px -3px 7px rgba(0, 0, 0, 0.7),
    6px 6px 12px rgba(0, 0, 0, 0.9);
  transition: all 0.25s ease;
  cursor: pointer;
  user-select: none;
  font-weight: 600;
  font-size: 1rem;
  border: 2px solid transparent;
}

/* Shine wave effect on hover */
.sidebar-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.3s ease;
  z-index: 0;
}
.sidebar-button:hover::before { left: 0; }

.sidebar-button:hover {
  background: linear-gradient(145deg, #1c1f2a, #2b2e3c);
  box-shadow:
    inset 2px 2px 6px rgba(255, 255, 255, 0.15),
    inset -2px -2px 6px rgba(0, 0, 0, 0.9),
    8px 8px 16px rgba(0, 0, 0, 1);
  transform: translateY(-2px) scale(1.02);
  z-index: 2;
}

/* Neon glow variants */
.sidebar-cyan { border: 2px solid #00ffff !important; }
.sidebar-cyan:hover { box-shadow: 0 0 10px #00ffff, 0 0 20px #00ffff, 0 0 30px #00ffff !important; }

.sidebar-purple { border: 2px solid #a855f7 !important; }
.sidebar-purple:hover { box-shadow: 0 0 10px #a855f7, 0 0 20px #a855f7, 0 0 30px #a855f7 !important; }

.sidebar-green { border: 2px solid #22c55e !important; }
.sidebar-green:hover { box-shadow: 0 0 10px #22c55e, 0 0 20px #22c55e, 0 0 30px #22c55e !important; }

.sidebar-yellow { border: 2px solid #facc15 !important; }
.sidebar-yellow:hover { box-shadow: 0 0 10px #facc15, 0 0 20px #facc15, 0 0 30px #facc15 !important; }

.sidebar-users { border: 2px solid #00e5ff !important; }
.sidebar-users:hover { box-shadow: 0 0 10px #00e5ff, 0 0 20px #00e5ff, 0 0 30px #00e5ff !important; }

.sidebar-logs { border: 2px solid #f97316 !important; }
.sidebar-logs:hover { box-shadow: 0 0 10px #f97316, 0 0 20px #f97316, 0 0 30px #f97316 !important; }

.sidebar-manual-push { border: 2px solid #8b5cf6 !important; }
.sidebar-manual-push:hover { box-shadow: 0 0 10px #8b5cf6, 0 0 20px #8b5cf6, 0 0 30px #8b5cf6 !important; }

.sidebar-red { border: 2px solid #ef4444 !important; }
.sidebar-red:hover { box-shadow: 0 0 10px #ef4444, 0 0 20px #ef4444, 0 0 30px #ef4444 !important; }

/* ===== Dashboard card shells ===== */
.dashboard-column {
  position: relative;
  padding: 1.5rem;
  background-color: rgba(0, 0, 0, 0.515);
  backdrop-filter: blur(10px);
  border-radius: 0.75rem;
  transition: all 0.3s ease;
  text-align: center;
  box-shadow: none;
  border: none;
  color: inherit;
  transform-origin: center center;
  margin: 0.25rem;
}
.dashboard-column-cyan { border: 2px solid #00ffff; }
.dashboard-column-cyan:hover { box-shadow: 0 8px 24px 0 #00ffff, 0 2px 8px 0 #00ffff; transform: scale(1.02); }

.dashboard-column-purple { border: 2px solid #a855f7; }
.dashboard-column-purple:hover { box-shadow: 0 8px 24px 0 #a855f7, 0 2px 8px 0 #a855f7; transform: scale(1.02); }

.dashboard-column-green { border: 2px solid #22c55e; }
.dashboard-column-green:hover { box-shadow: 0 8px 24px 0 #22c55e, 0 2px 8px 0 #22c55e; transform: scale(1.02); }

.dashboard-column-yellow { border: 2px solid #facc15; }
.dashboard-column-yellow:hover { box-shadow: 0 8px 24px 0 #facc15, 0 2px 8px 0 #facc15; transform: scale(1.02); }

.dashboard-column-teal { border: 2px solid #00e5ff; }
.dashboard-column-teal:hover { box-shadow: 0 8px 24px 0 #00e5ff, 0 2px 8px 0 #00e5ff; transform: scale(1.02); }

.dashboard-column-orange { border: 2px solid #f97316; }
.dashboard-column-orange:hover { box-shadow: 0 8px 24px 0 #f97316, 0 2px 8px 0 #f97316; transform: scale(1.02); }

.dashboard-column-red { border: 2px solid #ef4444; }
.dashboard-column-red:hover { box-shadow: 0 8px 24px 0 #ef4444, 0 2px 8px 0 #ef4444; transform: scale(1.02); }

.dashboard-column-darkpurple { border: 2px solid #ff0000; }
.dashboard-column-darkpurple:hover { box-shadow: 0 0 10px #f70707, 0 0 20px #ff0909, 0 0 30px #4f03ff; transform: scale(1.02); }

/* Prevent horizontal scroll */
body, html, #root {
  overflow-x: hidden;
  height: 100% !important;
  overflow-y: auto !important;
}

/* Smooth scaling for main area */
main {
  transition: transform 0.3s ease;
  min-height: 100vh !important;
  overflow-y: auto !important;
  padding-bottom: 2.5rem; /* space for bottom buttons */
}

/* Neon glow utility */
.neon-glow {
  box-shadow:
    0 0 2px currentColor,
    0 0 4px currentColor,
    0 0 6px currentColor,
    inset 0 0 2px currentColor;
  transition: box-shadow 0.3s ease;
}
.neon-glow:hover {
  box-shadow:
    0 0 4px currentColor,
    0 0 8px currentColor,
    0 0 12px currentColor,
    inset 0 0 4px currentColor;
}

/* Title wave */
.title-wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(34, 211, 238, 0.5), rgba(34, 211, 238, 0.2), transparent);
  background-size: 200% 100%;
  z-index: -1;
  border-radius: 0.75rem;
}

/* Shimmer wrapper */
.shimmer-wrapper {
  position: relative;
  overflow: hidden;
  background-image: none;
  border: 2px solid #00ffff;
  border-radius: 0.75rem;
  box-shadow: inset 4px 4px 8px rgba(255,255,255,0.08),
              inset -4px -4px 8px rgba(0,0,0,0.45),
              0 0 25px rgba(0,255,255,0.35);
}
.shimmer-wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 150%;
  height: 100%;
  background: linear-gradient(120deg, transparent, rgba(255,255,255,0.25), transparent);
  animation: shimmer-bar 2s infinite linear;
  pointer-events: none;
  display: none;
}
.dark-mode .shimmer-wrapper::before { display: block; }
.light-mode .shimmer-wrapper::before { display: none; }

@keyframes shimmer-bar {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Grid helpers */
.space-y-3 > div { display: contents; }
.grid-cols-4 { grid-template-columns: repeat(4, 1fr); }
.grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
.grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.grid-cols-1 { grid-template-columns: 1fr; }
.gap-3 { gap: 12px; }
.gap-7 { gap: 1.75rem; }

/* Text helpers */
.text-white-300 { color: #d1d5db; }
.text-white-200 { color: #e5e7eb; }
.text-white-400 { color: #9ca3af; }
.text-gray-400 { color: #9ca3af; }
.text-xs { font-size: 0.75rem; line-height: 1rem; }
.text-sm { font-size: 0.875rem; line-height: 1.25rem; }
.text-lg { font-size: 1.125rem; line-height: 1.75rem; }
.text-2xl { font-size: 1.5rem; line-height: 2rem; font-weight: 700; }
.font-semibold { font-weight: 600; }
.uppercase { text-transform: uppercase; }
.tracking-wide { letter-spacing: 0.025em; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mt-2 { margin-top: 0.5rem; }
.drop-shadow { filter: drop-shadow(0 1px 2px rgb(0 0 0 / 0.1)) drop-shadow(0 1px 1px rgb(0 0 0 / 0.06)); }

/* ===== Mobile-only (â‰¤ 1023px) ===== */
@media (max-width: 1023px) {
  .sidebar { left: -250px; }
  .sidebar.open { left: 0; }

  .sidebar-toggle-btn {
    position: fixed;
    top: 1rem;
    left: 1rem;
    z-index: 1100;
    background: rgba(26, 26, 46, 0.85);
    color: white;
    padding: 0.5rem 0.75rem;
    border-radius: 0.375rem;
    border: 1px solid #22d3ee;
    font-size: 1.25rem;
    cursor: pointer;
    display: block;
  }

  .sidebar-backdrop {
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,0.45);
    z-index: 1000;
    display: block;
  }

  .mobile-stack { flex-direction: column !important; }

  .dashboard-column {
    overflow-x: auto;
    overflow-y: visible;
  }
}

/* Light mode overrides */
.light-mode .text-white,
.light-mode .text-white-200,
.light-mode .text-white-300,
.light-mode .text-white-400,
.light-mode .text-gray-400 {
  color: #000 !important;
}
.light-mode .dashboard-column,
.light-mode .dashboard-column * {
  color: #000 !important;
  text-shadow: none !important;
  filter: none !important;
}
.light-mode .dashboard-column {
  background-color: rgba(255, 255, 255, 0.2) !important;
}
.light-mode .open-positions thead th {
  color: #fff !important;
}
.light-mode .sidebar {
  background: #ffffff !important;
  color: #000 !important;
  border-right: 2px solid #00d4ff;
}
.light-mode .sidebar ul li {
  color: #000 !important;
}

/* Dark mode text overrides */
.dark-mode .dashboard-column,
.dark-mode .dashboard-column * {
  color: #fff !important;
  text-shadow: none !important;
  filter: none !important;
}

/* Chart canvas text fixes (dark) */
.dark-mode canvas,
.dark-mode canvas *,
.dark-mode text {
  color: #fff !important;
  fill: #fff !important;
  -webkit-text-fill-color: #fff !important; /* Override internal styles */
  filter: none !important;
  background-color: transparent !important;
}

/* Glassy frame */
.glass-box {
  backdrop-filter: none;
  background: transparent;
  overflow: hidden;
  border: 5px solid #00ffff;
  border-radius: 0.75rem;
  box-shadow:
    0 0 10px #00ffff,
    0 0 20px #00ffff,
    0 0 40px #00ffff,
    inset 0 0 10px #00ffff;
  transition: box-shadow 0.3s ease;
}

/* Form transition helper */
.form-transition {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.3s ease, transform 0.3s ease;
  pointer-events: none;
  word-wrap: break-word;
  padding: 0 10px;
}
.form-transition.active {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}
.form-transition.inactive {
  opacity: 0;
  transform: translateY(20px);
  pointer-events: none;
}

/* Background video layer */
.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.3;
  z-index: -1;
}

/* Page glow wrapper */
.page-glow {
  position: relative;
}